// src/app/dashboard/page.tsx
"use client";

import { useState } from "react";
import { ChevronDown } from "lucide-react";
import {
  CircularProgressbar,
  buildStyles
} from "react-circular-progressbar";
import "react-circular-progressbar/dist/styles.css";

const OPTIONS = [
  "TOOL TEMP",
  "TOOL TEMP DEVICE",
  "HEAD TEMPERATURE N°1",
  "HEAD TEMPERATURE N°2",
  "FORCE",
  "Z POSITION",
  "ROTATION SPEED",
];

export default function DashboardPage() {
  const [selected, setSelected] = useState(OPTIONS[0]);
  // Pour l'instant la valeur est à 0
  const value = 0;

  return (
    <div className="p-8 bg-gray-100 h-full">
      <div className="w-80 mx-auto bg-white rounded-2xl p-4 shadow-lg">
        {/* Select */}
        <div className="relative">
          <select
            className="w-full border border-gray-300 rounded px-3 py-2 text-center text-lg font-bold appearance-none"
            value={selected}
            onChange={(e) => setSelected(e.currentTarget.value)}
          >
            {OPTIONS.map((opt) => (
              <option key={opt} value={opt}>
                {opt}
              </option>
            ))}
          </select>
          <ChevronDown
            size={20}
            className="absolute top-1/2 right-3 -translate-y-1/2 text-gray-500 pointer-events-none"
          />
        </div>

        {/* Progress bar semi-circulaire */}
        <div className="mt-4 h-48">
          <CircularProgressbar
            value={value}
            maxValue={100}
            text={`${value.toFixed(1)}`}
            circleRatio={0.5}
            styles={buildStyles({
              rotation:      1,             // démarre à 180°
              strokeLinecap: "butt",
              trailColor:    "#e5e7eb",     // couleur du fond non rempli
              pathColor:     "#8be78b",     // couleur du remplissage
              textColor:     "#071039",
              textSize:      "32px",
            })}
          />
        </div>
      </div>
    </div>
  );
}
